  </header>


  <!-- :Í≤åÏãúÎ¨º(Container.vueÏ™Ω) = "Í≤åÏãúÎ¨º"(data()Ï™Ω) -->
  <Container @write="ÏûëÏÑ±Í∏Ä=$event" :Í≤åÏãúÎ¨º="Í≤åÏãúÎ¨º" :step="step" :ÏóÖÎ°úÎìúÏù¥ÎØ∏ÏßÄ="ÏóÖÎ°úÎìúÏù¥ÎØ∏ÏßÄ" />
  <button v-if="step == 0" @click="moreData">Í≤åÏãúÎ¨º ÎçîÎ≥¥Í∏∞</button>


