      <!-- multiple : Îã§Ï§ëÏÑ†ÌÉù, accept="image/*" : ÏóÖÎ°úÎìú ÌååÏùº Ï¢ÖÎ•ò Ïù¥ÎØ∏ÏßÄ Î≤îÏúÑÎ°ú ÏÑ†ÌÉùÎê® -->
      <input @change="upload" accept="image/*" type="file" id="file" class="inputfile">
