      let moreData = () => {
        // Í≤ΩÎ°úÎ¨∏Ï†úÍ∞Ä Î∞úÏÉùÌï† Ïàò ÏûàÍ∏∞Ïóê then ÏïÑÎûò Íµ¨Î¨∏ÏóêÏÑú function ÎåÄÏã† ÌôîÏÇ¥ÌëúÌï®Ïàò ÏÇ¨Ïö©
        axios.get(`https://tarolong.github.io/more0.json`).then((res) => {
          // console.log(res.data);
          Í≤åÏãúÎ¨º.push(res.data);
          // Îç∞Ïù¥ÌÑ∞Ïùò Ïú†Ïã§Ïù¥ Ïö∞Î†§ÎêòÎäî Í≤ΩÏö∞, ÏúÑ Íµ¨Î¨∏Ïù¥ ÏïÑÎãå. try catch Íµ¨Î¨∏ÏùÑ Ïù¥Ïö©ÌïòÏó¨ ÏûëÏÑ±Ìï¥Î≥¥Ïûê.(ÏûëÏÑ±ÏïàÎê®)
        });
      };

      let upload = (event) => {
        // console.log(event.target.files);
        let temp = event.target.files[0]        
        let url = URL.createObjectURL(temp);
        ÏóÖÎ°úÎìúÏù¥ÎØ∏ÏßÄ.value = url;
        step.value = 1;
      };

      let publish = () => {
        let ÏÉàÍ±∞ = {
          name: "Kim Hyun",
          userImage: "https://placeimg.com/100/100/arch",
          postImage: ÏóÖÎ°úÎìúÏù¥ÎØ∏ÏßÄ.value,
          likes: 36,
          date: "May 15",
          liked: false,
          content:  ÏûëÏÑ±Í∏Ä.value,
          filter: Ï†ÄÏû•Ìï†ÌïÑÌÑ∞.value
        };
        // push()Îäî Îí∑ÏûêÎ¶¨Ïóê Ï∂îÍ∞ÄÎê®. ÏïûÏûêÎ¶¨ Ï∂îÍ∞ÄÎäî 'unshift()' ÏÇ¨Ïö©
        Í≤åÏãúÎ¨º.unshift(ÏÉàÍ±∞);
        step.value = 0;
      };

      return {
        Í≤åÏãúÎ¨º,
        step,
        ÏóÖÎ°úÎìúÏù¥ÎØ∏ÏßÄ,
        ÏûëÏÑ±Í∏Ä,
        Ï†ÄÏû•Ìï†ÌïÑÌÑ∞,
        moreData,
